/** Media Queries **/
header {
  background-color: red;
}
header .clear {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header h1 {
  padding: 0;
  margin: 0;
  text-align: center;
  font-size: 2rem;
  margin-top: 1rem;
  text-transform: uppercase;
  font-family: "Inter", sans-serif;
}
header h1 a {
  color: black;
}
header .btnMenu {
  margin-top: 1rem;
  background-image: url("/build/img/menus.webp");
  height: 32px;
  width: 32px;
}
header nav {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  animation: myAnim 1s ease 0s 1 normal forwards;
}
@keyframes myAnim {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
header nav a {
  display: block;
  width: auto;
  text-align: center;
  color: white;
  margin: 0.5rem 0;
}
header nav a:hover {
  cursor: pointer;
  text-decoration: underline;
}
header nav.mostrar {
  display: flex;
  color: blue;
}
@media (min-width: 768px) {
  header h1 {
    width: 100%;
    font-size: 2.5rem;
  }
  header .btnMenu {
    display: none;
  }
  header nav {
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
  header nav a {
    width: auto;
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  header h1 {
    font-size: 3rem;
  }
  header nav {
    margin-top: 0;
  }
  header nav a {
    font-size: 2.5rem;
  }
}
@media (min-width: 1440px) {
  header nav {
    margin-top: 2rem;
  }
}

.banner {
  margin-top: 2rem;
  height: 250px;
  width: 100%;
  position: relative;
  background-image: url("/build/img/banner.webp");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .banner {
    height: 350px;
  }
}
@media (min-width: 1024px) {
  .banner {
    height: 450px;
  }
}
@media (min-width: 1440px) {
  .banner {
    height: 550px;
  }
}

.banner-content {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.banner-content p {
  font-family: "Open Sans", sans-serif;
  color: white;
  margin: 0;
}
@media (min-width: 768px) {
  .banner-content p {
    font-size: 2rem;
  }
}
@media (min-width: 1440px) {
  .banner-content p {
    font-size: 3rem;
  }
}

.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo select,
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.contenedor h1 {
  font-size: 2.5rem;
}
.contenedor .prendas {
  margin: 5rem auto;
  width: 95%;
  height: 850px;
}
.contenedor .prendas .prenda {
  display: flex;
  flex-direction: column;
  width: 90%;
  padding: 1rem;
  margin: 0 auto;
  margin-bottom: 2rem;
  border-bottom: 0.2rem solid gray;
}
.contenedor .prendas .prenda h1 {
  font-size: 1.7rem;
  margin: 0;
}
.contenedor .prendas .prenda p {
  font-size: 1.5rem;
}
.contenedor .prendas .prenda img {
  height: 200px;
  border-radius: 0.5rem;
}
.contenedor .prendas .prenda .clear {
  margin: 0;
  display: flex;
  flex-direction: column;
  border: none;
}
.contenedor .prendas .prenda .clear p {
  margin: 0;
  margin-bottom: 2rem;
  padding: 0;
  font-size: 1.3rem;
}
.contenedor .prendas .prenda .clear p:last-of-type {
  margin: 0;
}
.contenedor .prendas .prenda .acciones {
  padding: 0;
  margin: 0;
  display: flex;
}
.contenedor .prendas .prenda .acciones .boton {
  flex: 1;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor h1 {
    font-size: 3rem;
    margin-top: 7rem;
  }
  .contenedor .prendas {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .contenedor .prendas .prenda {
    border-bottom: none;
    height: 300px;
    border-right: 0.2rem solid gray;
  }
  .contenedor .prendas .prenda h1 {
    font-size: 2rem;
  }
  .contenedor .prendas .prenda p {
    font-size: 1.8rem;
  }
  .contenedor .prendas .prenda .clear {
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
    margin: 0;
  }
  .contenedor .prendas .prenda .clear p {
    padding: 0;
    margin: 0;
    font-size: 1.5rem;
  }
  .contenedor .prendas .prenda .acciones {
    margin-top: 5rem;
    border: none;
    background-color: red;
  }
}
@media (min-width: 1024px) {
  .contenedor .prendas .prenda img {
    width: 500px;
    border-radius: 0.5rem;
  }
}
@media (min-width: 1440px) {
  .contenedor .prendas {
    width: 100%;
    margin: 0 auto;
    grid-template-columns: repeat(3, 1fr);
  }
  .contenedor .prendas .prenda .acciones .boton {
    width: 0;
  }
}

